	module.exports = {"data":{"markdownRemark":{"html":"<p>The whole EventRay team, all three of us, got together in Atlanta this past week for a coding “sprint.” It was pretty successful on all fronts: We accomplished five weeks of work, ate real bar-b-que<sup>1</sup>, chicken and waffles, and saw Marietta’s giant animatronic chicken.</p>\n<p><img src=\"/bigchicken2-3aa4e2dd72ccd5fb2315d906a13d1a08.jpg\" alt=\"big chicken\"> <em>It’s trying to eat me!</em>\n<img src=\"/cafesanfrancisco-9e666aba82674be7e0bbd8f73901fe4e.jpg\" alt=\"doing work\"> <em>Working from Cafe San Francisco (in Atlanta)</em></p>\n<p>We stayed at a place near Chattahoochee River National Park, so I got to check another national park off my list. Went running on the trails there one morning and saw a giant buck.</p>\n<p>It dawned on me that Atlanta has a November Project tribe, and that I could make their Wednesday morning workout before my flight home so I booked a hotel near Ponce City Market, where the workout would be held. I set my alarm before going to bed, then I woke up and saw 6:22 on my clock, eight minutes before the start! I never sleep through my alarm. I leapt out of bed, got dressed, ran outside and started booking it. Now, it was a special workout in that everyone was encouraged to bring a few cans of food for a local charity. So there I was, running pell-mell in the early morning twilight, carrying my four cans of Green Giant. I get to the meeting spot expecting to see a big crowd. No one. A minute later I see a couple guys running, so I hazard to guess they’re also looking for the workout. Turns out they are, and they’re somewhat familiar with the area, so after a bit of wandering around we come to a park and see some people running with cans. Success!</p>\n<p>The workout was designed to incorporate the cans: running with one can in each hand, doing lunges while shoulder pressing the cans, you get it… During the workout I got my shirt “tagged”<sup>2</sup> with the Atlanta tribe’s decal: ATL inscribed in a peach, then afterwards got some coffee with the friendly leaders of the Atlanta tribe and another visitor from Austin, Texas. Then I booked it back to the hotel to get ready for my flight.</p>\n<p><img src=\"/npatlgroup-2f99e53ad45f8ee7c4fc4a63f76744fa.jpg\" alt=\"NP ATL\"> <em>The workout crew</em>\n<img src=\"/npatlleaders-febb38783fb809a0f2de06041dde7fda.jpg\" alt=\"leaders, the traveller and me\"> <em>Myself, two of the leaders and the visitor from Austin</em></p>\n<hr>\n<p><small>footnotes</small></p>\n<h2></h2>\n<ol>\n<li>\n<small>\nFirst place we went to was a korean/southern bar-b-que fusion joint. We later decided that didn't really count and went to a place called D.B.A. in Virginia Highlands.\n</small>\n</li>\n<li>\n<small>\nSpray-painted. It's like a wearable passport book!\n</small>\n</li>\n</ol>","excerpt":"The whole EventRay team, all three of us, got together in Atlanta this past week for a coding “sprint.” It was pretty successful on all fronts: We accomplished five weeks of work, ate real bar-b-que 1 , chicken and waffles, and saw Marietta’s giant…","frontmatter":{"date":"November 15, 2017","path":"/blog/2017/11/15/atlanta","title":"Atlanta","category":"life, the universe and everything"}}},"pathContext":{}}

